body {

    height: 100vh;
    padding: 0;
    margin: 0;
}

header {

    display: flex;
    flex-direction: column;
    width: 100%;
    top: 0;
    background-color: rgb(156, 64, 53);
}

#title {
    display: flex;
    flex-direction: row;
    font-size: 60px;
    /* background-image: linear-gradient(to right,
            rgb(156, 64, 53),
            rgb(209, 85, 28)); */
    background-color: rgb(156, 64, 53);
    color: white;
    font-family: Monoton;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.titleBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.loggedInAs {
    color: white;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
    text-align: right;

}

#title:hover {
    color: rgb(200, 134, 48);
}

.capital {
    font-size: 72px;
}

#controls {
    display: flex;
    justify-content: space-between;
    /* background-color: rgb(175, 23, 23); */

    background-color: rgb(200, 134, 48);
    color: white;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
}

#searchbarcontainer {
    width: 60%;
}

#searchbar {
    width: 90%;
}

.link {
    padding: 5px;
    color: white;
    text-decoration: none;
    font-weight: bolder;
    font-size: large;
    cursor: pointer;
}

.link:hover {
    color: rgb(207, 207, 207);
}

.cartLink {
    position: relative;
    text-align: center;
    cursor: pointer;
}

.cartLink:hover {
    color: rgb(207, 207, 207);
}

.dropdown {
    padding-right: 5px;
}

.cartIcon {
    position: absolute;
    width: 30px;
    left: -25px;
    top: 2px;
}

.productNumber {
    font-size: 10px;
    text-align: center;
    position: absolute;
    left: -17px;
    top: 9px
}

#userLinks {
    display: flex;
    align-items: center;
}